html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-verde {
  background-color: #f599a8;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  border-radius: 40px;
  box-shadow: 12px -10px 15px 1px rgba(23, 11, 11, 0.55);
  -webkit-box-shadow: 12px -10px 15px 1px rgba(23, 11, 11, 0.55);
  -moz-box-shadow: 12px -10px 15px 1px rgba(23, 11, 11, 0.55);
}

.bg-amarillo {
  background-color: #f0b7e8;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  border-radius: 40px;
  box-shadow: 12px -10px 15px 1px rgba(23, 11, 11, 0.55);
  -webkit-box-shadow: 12px -10px 15px 1px rgba(23, 11, 11, 0.55);
  -moz-box-shadow: 12px -10px 15px 1px rgba(23, 11, 11, 0.55);
}

.header {
  background-color: #f53756;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    padding: 4rem 0;
  }
}
.header h1 {
  color: white;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #444;
}

.navegacion-principal {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.navegacion-principal a {
  color: white;
  font-size: 2rem;
}
.navegacion-principal a:hover {
  color: #f0b7e8;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient. Enlace permanente - úsalo para editar y compartir este gradiente: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+68,ffffff+100&0.8+0,0.5+100 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 68%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
.video video {
  width: 100%;
  display: block;
}
.video .contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  color: white;
  z-index: 1;
  position: relative;
}
.video .contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 4rem;
  font-weight: 400;
}
.video .contenido-video h2 span {
  background-color: #f53756;
  padding: 0.5rem 1rem;
  font-size: 2rem;
  font-weight: 300;
}

.sobre-festival {
  padding: 5rem 0;
  text-align: start;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 0;
  }
}
.contenido-festival h2 {
  font-weight: 700;
  font-size: 3rem;
}
.contenido-festival .fecha {
  font-weight: 700;
  color: #f53756;
}
.contenido-festival .texto {
  line-height: 1.5;
}

.lineup {
  background-image: radial-gradient(circle, #f53756, #fd6490, #fa8ec1, #f2b4e3, #efd7f7, #efd7f7, #efd7f7, #efd7f7, #f2b4e3, #fa8ec1, #fd6490, #f53756);
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #000;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
  color: white;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-size: 2rem;
  font-weight: 400;
}

.escenario {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}
.escenario .imagen-calendario {
  width: 20rem;
}

.calendario {
  padding: 0;
  width: 100%;
  list-style-type: none;
}
.calendario li {
  padding: 5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: #f06b80;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #e78ddb;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  margin-bottom: 10rem;
}

.galeria-imagenes {
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 5rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  justify-items: center;
  align-items: center;
}

.btn-galeria {
  width: 5rem;
  height: 5rem;
  font-size: 4rem;
  font-weight: 900;
  background-color: white;
  display: grid;
  place-items: center;
  border-radius: 50%;
}
.btn-galeria:hover {
  cursor: pointer;
}

.footer {
  background-color: #f53756;
  padding: 1rem;
  font-weight: 700;
  display: grid;
  place-items: center;
}
.footer p {
  color: white;
  margin: 0;
  font-size: 1.5rem;
}