.header {
    background-color: $rosa;


    .contenido-header {
        padding: 2rem;

        @include tablet {
            display: flex;
            justify-content: space-between;
            padding: 4rem 0;
        }
    }

    h1 {
        color: $blanco;
        text-align: center;
        margin-bottom: 2rem;

        @include tablet {
            margin-bottom: 0;
        }

    }

    &.fijo {
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .4rem #444;
    }
}

.navegacion-principal {

    display: flex;
    gap: 2rem;
    justify-content: center;

    a {
        color: $blanco;
        font-size: 2rem;

        &:hover {
            color: $amarillo;
        }
    }


}